import React from "react";
import { useState } from "react";
import ContributorInfo from "./../components/contributorInfo";
import ApiInfo from "./../components/apiInfo";

export default function AgeCalculator() {
  let [day, setDay] = useState("");
  let [month, setMonth] = useState("");
  let [year, setYear] = useState("");
  let [response, setResponse] = useState("");

  const search = () => {
    fetch(
      `https://starta11.herokuapp.com/calculate?day=${day}&month=${month}&year=${year}`
    )
      .then((res) => res.json())
      .then((data) => {
        setResponse(data);
      });
  };

  return (
    <div className="mx-4 my-8 rounded-md p-4 border-2 border-green-300 md:px-4">
      <h1 className="text-xl py-2 text-green-700 font-bold">
        Interplanetary Age Calculator
      </h1>
      <input
        type="text"
        name="day"
        placeholder=" Day "
        className="border-2 border-green-300 mr-4 py-2"
        value={day}
        onChange={(e) => {
          setDay(e.target.value);
        }}
      />
      &nbsp;&nbsp;&nbsp;&nbsp;
      <input
        type="text"
        name="month"
        placeholder=" Month "
        className="border-2 border-green-300 mr-4 py-2"
        value={month}
        onChange={(e) => {
          setMonth(e.target.value);
        }}
      />
      &nbsp;&nbsp;&nbsp;&nbsp;
      <input
        type="text"
        name="year"
        placeholder=" Year "
        className="border-2 border-green-300 mr-4 py-2"
        value={year}
        onChange={(e) => {
          setYear(e.target.value);
        }}
      />
      &nbsp;&nbsp;
      <button
        onClick={() => search()}
        className="bg-green-900 p-2 rounded-2xl text-white"
      >
        Search your Age
      </button>
      {response !== "" ? (
        <h1 className="text-2xl font-medium pb-6 pt-4">
          Mercury : {response.mercury} years&nbsp;&nbsp;&nbsp;&nbsp; Venus :{" "}
          {response.venus} years&nbsp;&nbsp;&nbsp;&nbsp; Earth :{" "}
          {response.earth} years&nbsp;&nbsp;&nbsp;&nbsp; <p />
          <br />
          Mars : {response.mars} years&nbsp;&nbsp;&nbsp;&nbsp; Jupiter :{" "}
          {response.jupiter} years&nbsp;&nbsp;&nbsp;&nbsp; Saturn :{" "}
          {response.saturn} years&nbsp;&nbsp;&nbsp;&nbsp;
          <p />
          <br />
          Neptune : {response.neptune} years&nbsp;&nbsp;&nbsp;&nbsp; Uranus :{" "}
          {response.uranus} years&nbsp;&nbsp;&nbsp;&nbsp; Pluto :{" "}
          {response.pluto} years&nbsp;&nbsp;&nbsp;&nbsp;
        </h1>
      ) : null}
      <ContributorInfo
        username="Thanos"
        profilePic="data:image/jpeg;base64,"
      />
      <ApiInfo
        apiUrl="https://www.exploratorium.edu/ronh/age/"
        apiName="Interplanetary Age"
      />
    </div>
  );
}
